/* Editor Label */

.monaco-workbench .part.editor > .content .editor-group-container > .title {
	cursor: pointer;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.title-label,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.tab-label {
	flex: 1;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.title-label
	.label-name,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.tab-label
	.label-name {
	white-space: nowrap;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.title-label
	a,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.tab-label
	a {
	font-size: 13px;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.monaco-icon-label::before,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.monaco-icon-label::before,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.title-label
	a,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.tab-label
	a,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.title-label
	h2,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.tabs-container
	> .tab
	.tab-label
	span {
	cursor: pointer;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.monaco-icon-label::before {
	height: var(--editor-group-tab-height); /* tweak the icon size of the editor labels when icons are enabled */
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title.breadcrumbs
	.monaco-icon-label::after,
.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title.tabs
	.monaco-icon-label::after {
	margin-right: 0; /* by default the icon label has a padding right and this isn't wanted when not showing tabs and not showing breadcrumbs */
}

/* Drag and Drop */

.monaco-editor-group-drag-image {
	display: inline-block;
	padding: 1px 7px;
	border-radius: 10px;
	font-size: 12px;
	position: absolute;
	/*
	 * Browsers apply an effect to the drag image when the div becomes too
	 * large which makes them unreadable. Use max width so it does not happen
	 */
	max-width: 120px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
